/** @format */

import { useState } from 'react'

interface LocalMessage {
  id: number
  text: string
  isUser: boolean
  timestamp: Date
}


export const useChat = () => {
  const [messages, setMessages] = useState<LocalMessage[]>([
    {
      id: 1,
      text: `AI`,
      isUser: false,
      timestamp: new Date()
    }
  ])

  const [isTyping, setIsTyping] = useState<boolean>(false)

  const sendMessage = async (content: string) => {
    if (!content.trim()) return

    // 
    const userMessage: LocalMessage = {
      id: Date.now(),
      text: content,
      isUser: true,
      timestamp: new Date()
    }

    setMessages((prev) => [...prev, userMessage])
    setIsTyping(true)

    try {
      //  GraphQL  Cloudflare Worker
      const response = await fetch(`${process.env.REACT_APP_API_URL}/graphql`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          query: `
            mutation SendMessage($message: String!) {
              sendMessage(message: $message) {
                message
                usage {
                  prompt_tokens
                  completion_tokens
                  total_tokens
                }
              }
            }
          `,
          variables: {
            message: content
          }
        })
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()

      //  GraphQL 
      if (data.errors) {
        throw new Error(data.errors[0].message || 'GraphQL ')
      }

      const sendMessageResult = data.data?.sendMessage
      if (!sendMessageResult) {
        throw new Error('GraphQL ')
      }

      // API 
      const aiMessage: LocalMessage = {
        id: Date.now() + 1,
        text: sendMessageResult.message || '',
        isUser: false,
        timestamp: new Date()
      }

      setMessages((prev) => [...prev, aiMessage])
    } catch (error) {
      console.error(':', error)

      // Fallback  Markdown 
      const fallbackResponses = [
        `"**${content}**"

###  API 



####  
- ****: \`http://localhost:8787/chat\`
- ****: POST
- ****: JSON

\`\`\`typescript
// Fetch 
const response = await fetch('http://localhost:8787/chat', {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
  },
  body: JSON.stringify({
    message: "${content}"
  })
});
\`\`\`

>  ****:  API `,

        `"**${content}**"

|  |  |  |
|---------|------|------|
|  Fetch API |   |  HTTP  |
|  Markdown |   |  |
|  UI  |   | Material-UI  |
|   |   |  |

###   API 

#### 
\`\`\`json
{
  "message": "",
  "model": "gpt-3.5-turbo",
  "temperature": 0.7,
  "max_tokens": 2000
}
\`\`\`

#### 
\`\`\`json
{
  "message": "AI ",
  "usage": {
    "total_tokens": 150
  }
}
\`\`\`

---
**`,

        `"**${content}**"

###  

#### 
- **React** + **TypeScript** - 
- **Material-UI** -  UI 
- **React Markdown** - 

#### 
\`\`\`javascript
//  fetch API
const sendMessage = async (content) => {
  try {
    const response = await fetch('/api/chat', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ message: content })
    });
    
    const data = await response.json();
    return data.message;
  } catch (error) {
    console.error(':', error);
    // 
  }
};
\`\`\`

###  
1. **** -  
2. **Markdown ** -    
3. **** -  
4. **UI ** -  
5. **** -  

> ****:  API ...

\`\`\`bash
# 
npm run start:server
# 
python app.py
\`\`\`

**** `,

        `"**${content}**"

###  

####  
- ****: 
- ****: 
- ****: 

\`\`\`typescript
// 
const [isTyping, setIsTyping] = useState(false);

const sendMessage = useCallback(async (content: string) => {
  if (isTyping) return; // 
  
  setIsTyping(true);
  try {
    // ...
  } catch (error) {
    // ...
  } finally {
    setIsTyping(false);
  }
}, [isTyping]);
\`\`\`

####  
- ****: 
- ****: 
- ****: ARIA 

###  
-  **** - 
-  **** - 
-  **** - 
-  **** - 

---
**`
      ]

      const randomResponse =
        fallbackResponses[Math.floor(Math.random() * fallbackResponses.length)]

      const fallbackMessage: LocalMessage = {
        id: Date.now() + 1,
        text: randomResponse,
        isUser: false,
        timestamp: new Date()
      }

      setMessages((prev) => [...prev, fallbackMessage])
    } finally {
      setIsTyping(false)
    }
  }

  return {
    messages,
    isTyping,
    sendMessage,
    setMessages
  }
}
